.advertiseCover {
  width: 100%;
  height: min-content;
  position: relative;
  display: flex;
  justify-content: center;
  margin: 0 auto 30px;
  img:first-of-type,
  img:last-of-type {
    position: absolute;
    height: 50px;
    width: 50px;
    top: calc(50% - 25px / 2);
    display: none;
  }
  img:nth-child(2) {
    width: 100%;
    height: min-content;
    max-width: 950px;
    max-height: 300px;
    object-fit: cover;
  }
  img:last-of-type {
    right: 0;
  }
}
